
import helper from '@/mixins/helper'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import html2canvas from 'html2canvas'

export default {
name: "share-img",
components: {XDialog},
mixins: [helper],
props: {
show: {
type: Boolean,
default: false
}
},
data() {
return {
showDialog: true
}
},
computed: {},
watch: {
show(value) {
this.showDialog = value
}
},
mounted() {

this.$nextTick(() => {
this.handleHtmlToCanvas()
})
},
methods: {

handleHtmlToCanvas() {
try {
let imgNode = this.$refs.imgNode,
imgBox = this.$refs.imgBox;

let imgNodeWidth = parseInt(imgNode.offsetWidth),
imgNodeHeight = parseInt(imgNode.offsetHeight);

let scaleBy = this.getDPR();

let canvas = document.createElement('canvas');

canvas.width = imgNodeWidth * scaleBy;
canvas.height = imgNodeHeight * scaleBy;

let context = canvas.getContext('2d');

context.scale(scaleBy, scaleBy);

html2canvas(imgNode).then(canvas => {
imgBox.innerHTML = '';
let newImg = document.createElement('img');
newImg.src = canvas.toDataURL('image/jpeg');
newImg.style.width = `${imgNodeWidth}px`
imgBox.appendChild(newImg);
})
} catch (e) {
console.log(e)
}
},


getDPR() {
if (window.devicePixelRatio && window.devicePixelRatio > 1) {
return window.devicePixelRatio;
}
return 1;
},
onShow() {
this.$emit('opened')
},
onHide() {
this.$emit('closed')
}
}
}



