
import helper from '@/mixins/helper'
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
name: "share-kl",
components: {XDialog},
mixins: [helper],
props: {
show: {
type: Boolean,
default: false
}
},
data() {
return {
showDialog: true
}
},
computed: {},
watch: {
show(value) {
this.showDialog = value
}
},
mounted() {

},
methods: {

handleCopyText(text) {
this.$copyText(text).then((e) => {
this.$vux.toast.text('复制成功', 'middle')
this.showDialog = false
}, (e) => {
console.log(e)
this.$vux.toast.text('复制失败', 'middle')
})
},
onShow() {
this.$emit('opened')
},
onHide() {
this.$emit('closed')
},
}
}



